<script>
  import { filter } from "../stores.js";
  import Filter from "./Filter.svelte";
  import SingleHeader from "./SingleHeader.svelte";

  export let account;
  export let feeds;
  let feed;
  let imgFile = account.files.filter((d) => d.format === "Item Image")[0];
  $: if ($filter && filter !== "") {
    feed = feeds.filter((d) => d.metadata.identifier === $filter)[0];
  } else {
    feed = account;
  }
</script>

<style>
  .show {
    font-weight: normal;
  }
</style>

<div class="row pt-3">
  <div class="col-12 col-md-6">
    <div class="d-flex flex-column flex-md-row align-items-center">
      <img
        class="mr-2"
        src={`https://archive.org/download/${account.metadata.identifier}/${imgFile.name}`}
        height="80px"
        width="auto"
        alt="logo" />
      <div
        class="d-flex flex-column flex-md-row w-100 align-items-center my-3 my-md-0">
        <h4 class="mx-2 my-2 my-md-0 show text-black-50">mostra</h4>
        <Filter />
      </div>
    </div>
  </div>
  <div class="col-12  col-md-6 ">
    {#if $filter}
      <SingleHeader
        compact
        podcast={feed.metadata}
        cover={feed.files.filter((d) => d.format === 'Item Image')[0]} />
    {:else}
      <div>
        <p>
          {@html feed.metadata.description}
        </p>
      </div>
    {/if}
  </div>
</div>
