<script>
  import { PlayFill } from "svelte-bootstrap-icons/lib/";
  import { tracks, playingTrack } from "../stores.js";
  import { fade } from "svelte/transition";

  function formatTime(rawTime) {
    const date = new Date(Math.round(rawTime) * 1000);
    return date.toUTCString().split(" ")[4];
  }

  function formatDate(rawDate) {
    const date = new Date(Math.round(rawDate) * 1000);
    return date.toLocaleDateString();
  }

  function setPlaying(track) {
    playingTrack.set(track);
  }
</script>

<style>
  .control {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .imgContainer:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }

  .imgContent {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .imgContent img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .title {
    font-size: 1.25rem;
  }
  .metadata {
    font-size: 0.75rem;
  }

  .author {
    font-size: 0.75rem;
  }

  @media (min-width: 768px) {
    .title {
      font-size: 2rem;
      text-transform: none;
      letter-spacing: -1px;
    }
    .metadata {
      font-size: 0.85rem;
    }
    .author {
      font-size: 0.85rem;
    }
  }
</style>

<div class="row pt-3 pt-md-0">
  {#if $tracks.length}
    {#each $tracks as track}
      <div class="col-12 col-md-12">
        <div class="p-3 my-1 my-md-3 border border-primary">
          <div class="row">
            <div class="col-5 col-md-3">
              <div class="position-relative imgContainer">
                <div class="imgContent">
                  <img
                    src={`https://archive.org/download/${track.identifier}/${track.cover.name}`}
                    alt="track cover" />
                  {#if ($playingTrack && $playingTrack.md5) !== track.md5}
                    <div
                      transition:fade
                      class="control d-flex align-items-center justify-content-center">
                      <button
                        class="btn btn-outline-light"
                        on:click={() => setPlaying(track)}><PlayFill
                          width="50px"
                          height="50px" /></button>
                    </div>
                  {/if}
                </div>
              </div>
            </div>
            <div class="col-7 col-md-6 d-flex flex-column">
              <h1 class="title">{track.title}</h1>
              <h6 class="author text-black-50">{track.identifierTitle}</h6>
              <div class="mt-auto">
                <p class="metadata mt-1 mb-0">{formatTime(track['length'])}</p>
                <p class="metadata mt-1 mb-0">{formatDate(track['mtime'])}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    {/each}
  {/if}
</div>
